@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600&display=swap');

/*This * means that it will apply to every single element*/
*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/*These are root variables that we can use anywhere in the project*/
:root {
  --color-bg: #b74854;
  --color-bg-variant: #a1585f;
  --color-primary: #c8737c;
  --color-primary-variant: rgba(77, 181, 255, .4);
  --color-white: #fff;
  --color-light: rgb(255, 255, 255);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar
{
  display: none;
}

body
{
  font-family: 'Quicksand', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-image: repeating-linear-gradient(223deg, rgba(0,0,0, 0.25), rgba(0,0,0, 0.25) 1px, transparent 0px, transparent 2px);
  background-size: 20px;
}


/* =================General Styles=================*/
.container
{
  width:var(--container-width-lg) ;
}

h1,h2,h3,h4,h5
{
  font-weight: 500;
}

h1
{
  font-size: 2.5rem;
}

section
{
  margin-top: 8rem;
}

section > h2, section > h5
{
  text-align: center;
  color: var(--color-light);
}

section > h2
{
  color: var(--color-light);
  margin-bottom: 3rem;
}

.text-light
{
  color: var(--color-light);
}

a
{
  color:var(---color-light);
  transition: var(--transition);
}

a:hover
{
  color: var(--color-white);
}

.btn
{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover
{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}


.btn-primary
{
  background: var(--color-primary);
  color: var(--color-bg);
}

img
{
  display: block;
  width: 100%;
  object-fit: cover;
}

/*===============Media Queries (Medium Devices) ===================*/
@media screen and (max-width:1024px) 
{
  .container
  {
    width: var(--container-width-md);
  }

  section
  {
    margin-top: 6rem;
  }
  
}

/*===============Media Queries (Small Devices) ===================*/
@media screen and (max-width:600px) 
{
  .container
  {
    width: var(--container-width-sm);
  }

  section > h2
  {
    margin-bottom: 2rem;
  }
  
}